import Runemark from '../Runemarks/Runemark'
import { shouldHideDropShadow } from '../state/printOptions';

const FighterCard = ({fighter, printOptions}) => {
	let klass = "fighter-card";
	if (shouldHideDropShadow(printOptions)) {
		klass += " no-shadow";
	} else {
		klass += " has-shadow";
	}

	return (
		<div className={klass}>
			<div className="rows">
				{fighter.long_name_format ? (
					<>
					<div className="name long">
						{fighter.name}
					</div>
					<div className="columns">
						<div className="column" />
						<div className="runemark-row">
							{fighter.runemarks.map((runemark) => (
								<div className="runemark-holder" key={runemark}>
									<Runemark runemark={runemark} />
								</div>
							))}
						</div>
						<div className="points">
							{fighter.points}
						</div>
						<div className="column" />
					</div>
					</>
				):(
					<div className="columns">
						<div className="name-container">
							<div className="name">
								{fighter.name}
							</div>
							<div style={{"flex": 1}} />
						</div>
						<div className="runemark-row">
							{fighter.runemarks.map((runemark) => (
								<div className="runemark-holder" key={runemark}>
									<Runemark runemark={runemark} />
								</div>
							))}
						</div>
						<div className="points">
							{fighter.points}
						</div>
					</div>
				)}
				<div className="columns">
					<div className="column">
						{fighter.weapons.map((weapon, idx) => (
							<div className="weapon-row" key={idx}>
								<Runemark runemark={weapon.runemark} />
								<div className="weapon-divider" > </div>
								<div className="runemark-value" >
									<Runemark runemark="characteristics-range" />
									<div className="value"> {weapon.range} </div>
								</div>
								<div className="weapon-divider" > </div>
								<div className="runemark-value" >
									<Runemark runemark="characteristics-attacks" />
									<div className="value"> {weapon.attacks} </div>
								</div>
								<div className="weapon-divider" > </div>
								<div className="runemark-value" >
									<Runemark runemark="characteristics-strength" />
									<div className="value"> {weapon.strength} </div>
								</div>
								<div className="weapon-divider" > </div>
								<div className="runemark-value" >
									<Runemark runemark="characteristics-damage" />
									<div className="value">
										{weapon.normal_damage}/{weapon.critical_damage}
									</div>
								</div>
							</div>
						))}
					</div>
					<div className="column">
						<div className="stats-row">
							<div className="runemark-value" >
								<Runemark runemark="characteristics-move" />
								<div className="value"> {fighter.move} </div>
							</div>
							<div className="weapon-divider" > </div>
							<div className="runemark-value" >
								<Runemark runemark="characteristics-toughness" />
								<div className="value"> {fighter.toughness} </div>
							</div>
							<div className="weapon-divider" > </div>
							<div className="runemark-value" >
								<Runemark runemark="characteristics-wounds" />
								<div className="value"> {fighter.wounds} </div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default FighterCard;
