import { useState } from 'react';

import Editor from './Editor/Editor';
import Roster from './Roster/Roster';
import { getDefaultFighter, getDefaultAbility} from './defaultValues';

const RosterBuilder = ({
	fighters,
	abilities,
	saveAbility,
	saveFighter,
	moveAbility,
	moveFighter,
	deleteAbility,
	deleteFighter,
}) => {
	const [ currentlyEditing, setCurrentlyEditing ] = useState(null);

	const createFighter = () => {
		const newId = 1 + Math.max(0, ...fighters.map((fighter) => fighter.id));
		setCurrentlyEditing({
			type: "FIGHTER",
			value: getDefaultFighter(newId)
		});
	}	   
			
	const createAbility = () => {
		const newId = 1 + Math.max(0, ...abilities.map((ability) => ability.id));
		setCurrentlyEditing({ 
			type: "ABILITY",
			value: getDefaultAbility(newId)
		}); 
	}   


	const exitEditor = () => {
		setCurrentlyEditing(null);
	}

	return (
		<div className="ReferenceSheetBuilder no-print">
			<Editor
				currentlyEditing={currentlyEditing}
				createFighter={createFighter}
				createAbility={createAbility}
				saveFighter={saveFighter}
				saveAbility={saveAbility}
				deleteFighter={deleteFighter}
				deleteAbility={deleteAbility}
				exitEditor={exitEditor}
			/>
			<Roster
				fighters={fighters}
				abilities={abilities}
				setCurrentlyEditing={setCurrentlyEditing}
				moveFighter={moveFighter}
				moveAbility={moveAbility}
				deleteFighter={deleteFighter}
				deleteAbility={deleteAbility}
			/>
		</div>
	);
}

export default RosterBuilder;
